    padding: 0;
    margin: 0;
    box-sizing: 0;
    text-decoration: none;
    font-family: sans-serif;
}

header {
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 50px;
}

.navbar-brand {
    font-weight: 500;
    font-family: Ubuntu, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.navbar-brand img {
    transition: 0.5s;
}

.navbar-brand img:hover {
    rotate: -20deg;
}

.navbar-nav .btn-primary {
    background-color: #030804;
    border: 1px solid transparent;
    border-radius: 5px;
    height: 32px;
    transition: none;
    color: #fff;
    font-weight: bold;
    margin-top: 4px;
    width: 12em;
    margin-left: 32px;
}

.navbar-nav .btn-primary:hover {
    background-color: #030804;
    border: 1px solid transparent;
    border-radius: 5px;
    height: 32px;
    transition: none;
    color: #fff;
    font-weight: bold;
    margin-top: 4px;
    width: 12em;
}

.nav-link {
    margin-left: 32px;
}

.jumbotron {
    margin-top: 10em;
    font-family: 'Open Sans', Verdana, Geneva, Tahoma, sans-serif;
}

.tagline1,
.tagline2 {
    font-size: 3em;
    font-weight: 700;
}

.tagline1 span,
.tagline2 span {
    color: #42da49;
}

.lead {
    font-weight: 400;
    font-size: 1.4em;
}

.btn-primary {
    background-color: #42da49;
    border: 1px solid transparent;
    border-radius: 3rem;
    font-weight: 100;
    transition: 0.8s;
}

.btn-primary:hover {
    background-color: #fff;
    border: 1px solid #42da49;
    border-radius: 3rem;
    color: #42da49;
    font-weight: 100;
}

.footer {
    background-color: #ffffff;
    text-align: center;
    position: fixed;
    z-index: 2;
    width: 100%;
    font-size: 12px;
    bottom: 0;
}

@media screen and (max-width: 979px) {

    .tagline1,
    .tagline2 {
        font-size: 2.2em;
    }

    .lead {
        font-size: 18px;
    }
}

@media screen and (max-width: 765px) {

    .tagline1,
    .tagline2 {
        font-size: 1.8em;
    }

    .lead {
        font-size: 16px;
    }
}

@media screen and (max-width: 519px) {

    .tagline1,
    .tagline2 {
        font-size: 1.7em;
    }

    .lead {
        font-size: 16px;
    }
}

@media screen and (max-width: 435px) {

    .tagline1,
    .tagline2 {
        font-size: 1.4em;
    }

    .lead {
        font-size: 15px;
    }
}

@media screen and (max-width: 345px) {

    .tagline1,
    .tagline2 {
        font-size: 1.1em;
    }

    .lead {
        font-size: 12px;
    }
}

/* Pages */
  body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background-color: #f2f2f2;
  }
  
  .container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;
    margin-top: 20px;
  }
  
  .calculator {
    width: 400px;
    padding: 20px;
    margin-left: 10px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin-bottom: 5em;
  }
  
  .calculator h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .calculator .input-group {
    margin-bottom: 10px;
  }
  
  .calculator label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  .calculator input {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .calculator button {
    width: 100%;
    padding: 10px;
    background-color: #42da49;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: 1s;
  }
  
  .calculator button:hover {
    border: 1px solid #42da49;
    background-color: #ffffff;
    color: #42da49;
  }
  
  .calculator #result {
    margin-top: 10px;
    text-align: center;
    font-weight: bold;
  }
  
  .history {
    width: 100%;
    max-height: 545px;
    overflow-y: auto;
  }
  
  table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    border: 1px solid #ddd;
  }
  
  th,
  td {
    text-align: center;
    padding: 16px;
  }
  
  th {
    background-color: orange;
    color: white;
  }
  
  tr:nth-child(even) {
    background-color: #d2d2d2;
  }
  
  .card {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    padding: 30px;
    background-color: #fff;
    margin-right: 20px;
  }
  
  .card button {
    width: 100%;
    padding: 10px;
    background-color: #42da49;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: 1s;
  }
  
  .card button:hover {
    border: 1px solid #42da49;
    background-color: #ffffff;
    color: #42da49;
  }

