    .container {
      flex-direction: column;
      align-items: center;
    }

    .calculator {
      width: 100%;
      margin: 0;
      margin-bottom: 20px;
    }

    .history {
      width: 100%;
      max-height: none;
    }

    .card {
      margin-right: 0;
    }
